/** **   request.js   ** **/
// 导入axios
import axios from 'axios'

import { Message } from 'element-ui'

// 1. 创建新的axios实例，
const service = axios.create({
  // 公共接口
  baseURL: process.env.BASE_API
  // 超时时间 单位是ms，这里设置了10s的超时时间
  // timeout: 10 * 1000
})
// 2.请求拦截器
service.interceptors.request.use(config => {
  // 发请求前做的一些处理，数据转化，配置请求头，设置token,设置loading等
  // config.data = JSON.stringify(config.data)
  config.headers = {
    // 'Content-Type': 'application/x-www-form-urlencoded',    formdata方式向后台传数据
    // 'Content-Type': 'application/json'   request payload方式向后台传数据
    'Content-Type': 'application/json',
    'Cache-Control': 'no-cache'
  }
  return config
}, error => {
  Promise.reject(error)
})

// 3.响应拦截器
service.interceptors.response.use(response => {
  // 接收到响应数据并成功后的一些共有的处理，关闭loading等
  if (response.data.code === 50000) {
    Message.error('登录状态失效，请重新登录')
  } else if (response.data.code !== 200 && response.data.code !== 50000) {
    Message.error('未知错误，请刷新页面或者联系管理员')
  } else if (response.data.code === 200 && response.data.status === 0) {
    Message.error(response.data.msg)
  }
  return response
}, error => {
  /** *** 接收到异常响应的处理开始 *** **/
  if (error && error.response) {
    // 1.公共错误处理
    // 2.根据响应码具体处理
    switch (error.response.status) {
      case 400:
        Message.error('错误请求')
        break
      case 401:
        Message.error('未授权，请重新登录')
        break
      case 403:
        Message.error('拒绝访问')
        break
      case 404:
        Message.error('请求错误,未找到该资源')
        // window.location.href = "/NotFound"
        break
      case 405:
        Message.error('请求方法未允许')
        break
      case 408:
        Message.error('请求超时')
        break
      case 500:
        Message.error('服务器端出错')
        break
      case 501:
        Message.error('网络未实现')
        break
      case 502:
        Message.error('网络错误')
        break
      case 503:
        Message.error('服务不可用')
        break
      case 504:
        Message.error('网络超时')
        break
      case 505:
        Message.error('http版本不支持该请求')
        break
      default:
        Message.error(`连接错误${error.response.status}`)
    }
  } else {
    // 超时处理
    // if (JSON.stringify(error).includes('timeout')) {
    //   Message.error('服务器响应超时，请刷新当前页')
    // }
    Message.error('连接服务器失败')
  }
  /** *** 处理结束 *** **/
  // 如果不需要错误处理，以上的处理过程都可省略
  return Promise.resolve(error.response)
})
// 4.导入文件
export default service



