<template>
  <div class="allDiv">
    <div class="condition">
      需要加密或者解密的文本：<el-input v-model="str" class="inputTitle" placeholder="需要加密或者解密的文本"></el-input>
      加密或者解密之后的文本：<el-input v-model="newStr" class="inputTitle" placeholder="加密或者解密之后的文本"></el-input>
      <el-button type="primary" class="search" @click="sm4StringCBC(1)">加密</el-button>
    </div>
    <div class="condition">
      &#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;密钥：<el-input v-model="secretKey" class="inputTitle" placeholder="密钥"></el-input>
      &#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;iv：<el-input v-model="iv" class="inputTitle" placeholder="iv"></el-input>
      <el-button type="success" class="search" @click="sm4StringCBC(2)">解密</el-button>
    </div>
    <div class="condition">
    </div>
    <el-table class="tableList"
      max-height="640"
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        prop="str"
        label="需要加密或者解密的文本"
        align="center">
      </el-table-column>
      <el-table-column
        prop="newStr"
        label="加密或者解密之后的文本"
        align="center">
      </el-table-column>
      <el-table-column
        prop="secretKey"
        label="密钥："
        align="center">
      </el-table-column>
      <el-table-column
        prop="iv"
        label="iv"
        align="center">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
// 解密和加密
import api from '../utils/api'

export default {

  data () {
    return {
      str: null,
      newStr: null,
      secretKey: null,
      iv: null,
      tableData: []
    }
  },
  methods: {
    sm4StringCBC (num) {
      let data = null
      if (num === 1) {
        data = {
          str: this.str
        }
      } else {
        data = {
          str: this.str,
          secretKey: this.secretKey,
          iv: this.iv
        }
      }
      api.sm4StringCBC(data)
        .then(res => {
          if (res.data.code === 200 && res.data.status === 1) {
            this.tableData.push(res.data.data)
            this.str = res.data.data.str
            this.newStr = res.data.data.newStr
            this.secretKey = res.data.data.secretKey
            this.iv = res.data.data.iv
          }
        })
        .catch(err => console.log(err))
      return false
    }
  },
  created () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.allDiv{
  padding: 12px 30px 0px 30px;
  height: calc(100% - 53px);
  width: calc(100% - 60px);
}
.title{
  font-size: 18px;
  font-family: Microsoft YaHei;
  font-weight: bold;
  color: #333333;
}
.condition{
  line-height: 60px;
  border-bottom: 1px solid #F4F3F3;
  font-size: 16px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  color: #333333;
}
.inputTitle{
  width: 400px;
  margin-right: 20px;
}
</style>



