/**
 *  所有的请求接口
 */
import http from './http'
/**
 *  @parms resquest 请求地址 例如：http://197.82.15.15:8088/request/...
 * @param '/api'代表vue-cil中config，index.js中配置的代理
 */
// 开发时用代理
// let resquest = '/api'
// 打包后用这个
let resquest = '/sm2AndSm4'

// get请求
export default {
  // 加密解密文件
  sm2StringSymmetry (params) {
    return http.post(`${resquest}/sm/sm2StringSymmetry`, params)
  },
  sm2StringAsymmetric (params) {
    return http.post(`${resquest}/sm/sm2StringAsymmetric`, params)
  },
  sm4StringECB (params) {
    return http.post(`${resquest}/sm/sm4StringECB`, params)
  },
  sm4StringCBC (params) {
    return http.post(`${resquest}/sm/sm4StringCBC`, params)
  },
  sm4 (params) {
    return http.post(`${resquest}/sm/sm4`, params)
  }

}



