import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import index1 from '@/components/index1'
import index2 from '@/components/index2'
import index3 from '@/components/index3'
import index4 from '@/components/index4'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      meta: {
        title: '利用sm2String对称加密'
      }
    },
    {
      path: '/1',
      name: 'index1',
      component: index1,
      meta: {
        title: '利用sm2String非对称加密'
      }
    },
    {
      path: '/2',
      name: 'index2',
      component: index2,
      meta: {
        title: '利用sm4StringECB加密'
      }
    },
    {
      path: '/3',
      name: 'index3',
      component: index3,
      meta: {
        title: '利用sm4StringCBC加密'
      }
    },
    {
      path: '/4',
      name: 'index4',
      component: index4,
      meta: {
        title: '利用sm4加密文件'
      }
    }
  ]
})



