<template>
  <div class="allDiv">
    <div class="condition">
      需要加密或者解密的文本：<el-input v-model="str" class="inputTitle" placeholder="需要加密或者解密的文本"></el-input>
    </div>
    <div class="condition">
      &#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;公钥：<el-input v-model="gong" class="inputTitle" placeholder="公钥"></el-input>
      &#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;私钥：<el-input v-model="si" class="inputTitle" placeholder="私钥"></el-input>
      <el-button type="primary" class="search" @click="sm2StringAsymmetric(1)">加密</el-button>
    </div>
    <div class="condition">
      &#12288;&#12288;&#12288;&#12288;软加密签名结果：<el-input v-model="ruan" class="inputTitle" placeholder="软加密签名结果"></el-input>
      &#12288;&#12288;&#12288;&#12288;加密机签名结果：<el-input v-model="jia" class="inputTitle" placeholder="加密机签名结果"></el-input>
      <el-button type="success" class="search" @click="sm2StringAsymmetric(2)">验证</el-button>
    </div>
    <div class="condition">
    </div>
    <el-table class="tableList"
      max-height="640"
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        prop="str"
        label="需要加密或者解密的文本"
        align="center">
      </el-table-column>
      <el-table-column
        prop="gong"
        label="公钥"
        align="center">
      </el-table-column>
      <el-table-column
        prop="si"
        label="私钥"
        align="center">
      </el-table-column>
      <el-table-column
        prop="ruan"
        label="软加密签名结果"
        align="center">
      </el-table-column>
      <el-table-column
        prop="jia"
        label="加密机签名结果"
        align="center">
      </el-table-column>
      <el-table-column
        prop="jia"
        label="加密机签名结果"
        align="center">
      </el-table-column>
      <el-table-column
        prop="jia"
        label="加密机签名结果"
        align="center">
      </el-table-column>
      <el-table-column
        prop="newStrRuan"
        label="软件加密方式验签结果"
        align="center">
      </el-table-column>
      <el-table-column
        prop="newStrYing"
        label="硬件加密方式验签结果"
        align="center">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
// 解密和加密
import api from '../utils/api'

export default {

  data () {
    return {
      str: null,
      newStr: null,
      gong: null,
      si: null,
      ruan: null,
      jia: null,
      tableData: []
    }
  },
  methods: {
    sm2StringAsymmetric (num) {
      let data = null
      if (num === 1) {
        data = {
          str: this.str
        }
      } else {
        data = {
          str: this.str,
          gong: this.gong,
          si: this.si,
          ruan: this.ruan,
          jia: this.jia
        }
      }
      api.sm2StringAsymmetric(data)
        .then(res => {
          if (res.data.code === 200 && res.data.status === 1) {
            this.tableData.push(res.data.data)
            console.log(this.tableData)
            this.str = res.data.data.str
            this.gong = res.data.data.gong
            this.si = res.data.data.si
            this.ruan = res.data.data.ruan
            this.jia = res.data.data.jia
          }
        })
        .catch(err => console.log(err))
      return false
    }
  },
  created () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.allDiv{
  padding: 12px 30px 0px 30px;
  height: calc(100% - 53px);
  width: calc(100% - 60px);
}
.title{
  font-size: 18px;
  font-family: Microsoft YaHei;
  font-weight: bold;
  color: #333333;
}
.condition{
  line-height: 60px;
  border-bottom: 1px solid #F4F3F3;
  font-size: 16px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  color: #333333;
}
.inputTitle{
  width: 400px;
  margin-right: 20px;
}
</style>



