<template>
  <div class="allDiv">
    <div class="title">对称加密</div>
    <div class="condition">
      &#12288;&#12288;&#12288;文件原本路径：<el-input v-model="oldFile" class="inputTitle" placeholder="文件原本路径(比如 D:\\zzz\\zzz.zip)"></el-input>
      &#12288;&#12288;&#12288;文本现在路径：<el-input v-model="newFile" class="inputTitle" placeholder="文本现在路径"></el-input>
      <el-button type="primary" class="search" @click="sm4(1)">加密</el-button>
    </div>
    <div class="condition">
      &#12288;&#12288;&#12288;&#12288;&#12288;&#12288;&#12288;密钥：<el-input v-model="key" class="inputTitle" placeholder="密钥"></el-input>
      <el-button type="success" class="search" @click="sm4(2)">解密</el-button>
    </div>
    <div class="condition">
    </div>
    <el-table class="tableList"
      max-height="640"
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        prop="oldFile"
        label="文件原本路径"
        align="center">
      </el-table-column>
      <el-table-column
        prop="newFile"
        label="文本现在路径"
        align="center">
      </el-table-column>
      <el-table-column
        prop="key"
        label="公钥"
        align="center">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
// 解密和加密
import api from '../utils/api'

export default {

  data () {
    return {
      oldFile: null,
      newFile: null,
      key: null,
      tableData: []
    }
  },
  methods: {
    sm4 (num) {
      let data = null
      if (num === 1) {
        data = {
          oldFile: this.oldFile,
          newFile: this.newFile
        }
      } else {
        data = {
          oldFile: this.oldFile,
          newFile: this.newFile,
          key: this.key
        }
      }
      api.sm4(data)
        .then(res => {
          if (res.data.code === 200 && res.data.status === 1) {
            this.tableData.push(res.data.data)
            this.oldFile = res.data.data.oldFile
            this.newFile = res.data.data.newFile
            this.key = res.data.data.key
          }
        })
        .catch(err => console.log(err))
      return false
    }
  },
  created () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.allDiv{
  padding: 12px 30px 0px 30px;
  height: calc(100% - 53px);
  width: calc(100% - 60px);
}
.title{
  font-size: 18px;
  font-family: Microsoft YaHei;
  font-weight: bold;
  color: #333333;
}
.condition{
  line-height: 60px;
  border-bottom: 1px solid #F4F3F3;
  font-size: 16px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  color: #333333;
}
.inputTitle{
  width: 400px;
  margin-right: 20px;
}
</style>



